:root {
    --headerBorderColor: #e1e1e1;
    --orangeColor: #ff811a;
    --primaryColor: #fff;
    --primaryTextColor: #828282;
}

body {
    margin: 0;
    background-color: #f9f9f9;
    font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
    letter-spacing: .01em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@media print {
    @page {
        size: A4 landscape;
    }

    header, #weekSlider, #footer {
        display: none;
    }

    .day, .type, .meal, .workout, .guilt-free {
        max-width: 150px;
    }
}